body::-webkit-scrollbar {
  display: none;
}

* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  background-color: #F6F7F9;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*:focus {
    outline: none;
}

.header{
    width: 100vw;
    height: 124px;
    background: white;
    display: flex;
    align-items: center;
}

.header__logo {
    margin: 3.75vh 2.5vw;
}

.header__content{
    display: flex;
    flex: 1 1;
    align-items: center;
    justify-content: space-between;
    margin-right: 2.5rem;
}
.header__right{
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 260px;
    flex: 1 1;
}

.header__right > button {
    font-size: 18px;
    background: #3D5278;
    color: white;
    font-weight: 600;
    border: none;
    border-radius: 20px;
    padding: 4px 14px;
    height: 44px;
    margin: 0px 10px;
}

.header__right > button:hover {
   scale: 1.1;
}

.header__middle{
    display: flex;
    flex: 1 1;
    max-width: 492px;
}
.search__container{
    color: #3D5278;
    border: 1px solid #C3D4E9;
    border-width: 1px;
    border-radius: 70px;
    max-height: 44px;
    display: flex;
    padding: 10px 20px;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-right: 2.5vw;
}
.header__search{
    background: transparent;
    flex: 1 1;
    width: 100%;
    font-size: 14px;
    min-width: 48px;
    margin-left: 0.675vw;
    border: none;
}


.search__container > input :focus{
    outline: none;
    background: red;
}

.header__feature{
    color: #3D5278;
    border: 1px solid #C3D4E9;
    border-radius: 90px;
    padding: 9px;
    margin: 0px 0.625vw;
}

.header__avatar,
.header__feature:hover {
    scale: 1.1;
    cursor: pointer;
    transition: 200ms ease-in-out;
}

.header__avatar {
    border-radius: 22px;
    height: 44px;
   margin-left: 10px
}


.app{
    display: flex;
    flex-direction: column;
}

.content{
    display: flex;
}
